@font-face {
  font-family: Alte Haas;
  font-style: normal;
  font-weight: normal;
  src: url("./assets/AlteHaasGroteskBold.ttf") format("trueType");
}

.video {
  position: relative;
  width: 100%;
  height: 100%;
}
@media screen and (max-width: 699px) {
  .video {
    max-width: 699px;
  }
}

.content {
  position: absolute;
  top: 0;
  font-family: Alte Haas, sans-serif;
  line-height: 0.1em;
  color: #fcfce0;
  text-shadow: 2px 2px 6px rgba(2, 2, 2, 0.596);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
  padding: 0;
}
@media screen and (max-width: 699px) {
  .content {
    font-size: 1.5em;
    left: auto;
  }
}

.bandcamp {
  width: 520px;
  height: 120px;
}
@media screen and (max-width: 699px) {
  .bandcamp {
    width: 300px;
  }
}

#main {
  font-size: 3em;
}
@media screen and (max-width: 699px) {
  #main {
    font-size: 1.5em;
    left: auto;
  }
}
#promenade {
  font-size: 5em;
}
@media screen and (max-width: 699px) {
  #promenade {
    font-size: 1.7em;
    left: auto;
  }
}

#hundebiss {
  font-size: 2em;
}
@media screen and (max-width: 699px) {
  #hundebiss {
    font-size: 0.75em;
    left: auto;
  }
}
